:root {
  --color-grey: #757575;
  --color-black: #212121;
  --color-accent: #2196f3;
  --font-logo: "Raleway";
  --color-white: #fff;
  --bcg-dark: #2f303a;
  --bcg-white: #f5f5f5;
  --bcg-grey: #f5f4fa;
  --icon-fill: #afb1b8;
  --border-grey: #eee;
  --trans: 250ms cubic-bezier(.4, 0, .2, 1);
  --trans-slow: 400ms cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  color: var(--color-black);
  letter-spacing: .03em;
  background-color: var(--color-white);
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.list {
  list-style-type: none;
}

.link {
  color: inherit;
  -webkit-text-decoration-line: none;
  text-decoration-line: none;
}

.accent {
  color: var(--color-accent);
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

@media screen and (min-width: 1200px) {
  .section {
    padding-top: 94px;
    padding-bottom: 94px;
  }
}

.no-scroll {
  overflow: hidden;
}

.hide {
  clip: rect(1px, 1px, 1px, 1px);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  width: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
  word-wrap: normal !important;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.btn-hero {
  letter-spacing: .06em;
  min-width: 216px;
  height: 50px;
  color: var(--color-white);
  background: var(--color-accent);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.875;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.btn {
  letter-spacing: .03em;
  text-align: center;
  min-width: 73px;
  height: 38px;
  background: var(--bcg-grey);
  border: var(--bcg-grey);
  transition: background-color var(--trans), color var(--trans), box-shadow var(--trans);
  border-radius: 4px;
  padding-left: 22px;
  padding-right: 22px;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.625;
}

.btn:hover, .btn:focus {
  background-color: var(--color-accent);
  color: var(--color-white);
  cursor: pointer;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.btn-form {
  width: 200px;
  height: 50px;
  background: #2196f3;
  border-radius: 4px;
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.backdrop {
  z-index: 10;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity var(--trans-slow);
  background: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(1.2);
}

.modal {
  transition: transform var(--trans-slow);
  width: 95%;
  max-width: 450px;
  height: 95%;
  max-height: 609px;
  background: #fff;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: scroll;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
    height: 521px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
    height: 581px;
  }
}

