.footer-form {
  display: flex;
  margin-left: 0;
  margin-top: 60px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  width: 450px;
  @include desktop {
    margin-left: 93px;
    align-items: flex-end;
  }
}

