.footer-form-label {
  display: flex;
  flex-wrap: wrap;
  font-weight: 700;
  font-size: 14px;
  line-height: calc(16 / 14);
  text-transform: uppercase;
  color: #ffffff;
  gap: 20px;
  justify-content: center;
  width: 100%;
  @include desktop {
    width: 358px;
    justify-content: flex-start;
  }
}

.footer-form-input {
  width: 100%;
  height: 50px;
  font-weight: 400;
  font-size: 16px;
  line-height: calc(20 / 16);
  padding-left: 16px;
  color: rgba(255, 255, 255, 0.6);
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.3);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
  border-radius: 4px;

  &:focus {
    outline: 1px solid var(--color-accent);
    border-radius: 4px;
  }
}

.footer-form-btn {
  position: relative;

  width: 200px;
  height: 50px;
  text-align: start;
  padding-left: 28px;
  color: var(--color-white);
  background: var(--color-accent);
  cursor: pointer;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  border: none;
  font-weight: 700;
  font-size: 16px;
  line-height: calc(30 / 16);
  letter-spacing: 0.06em;
  margin-left: 0;
  @include desktop {
    margin-left: 12px;
  }
}

.footer-form-icon {
  fill: var(--color-white);
  position: absolute;
  top: 13px;
  right: 28px;
}
