.footer {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: var(--bcg-dark);
  color: var(--color-white);
}

.footer-block {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  // justify-content: space-around;
  justify-content: center;
  // @include tablet {
  //   height: 464px;
  // }
  @include desktop {
    flex-wrap: nowrap;
  }
}

.address {
  margin-top: 20px;
  font-style: normal;
  line-height: calc(24 / 14);
}

.address-link {
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
  @include desktop {
    text-align: left;
  }
}

.address-item.mail {
  margin-top: 9px;
  margin-bottom: 9px;
}

.logo-address {
  margin-top: 28px;
  width: 236px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  @include desktop {
    justify-content: flex-start;
  }
}

.address-link,
.geopzition-link {
  transition: color var(--trans);
  width: 231px;
  display: block;
  text-align: center;

  &:hover,
  &:focus {
    color: var(--color-accent);
  }

  @include desktop {
    text-align: start;
  }
}

.socialmesh-footer {
  margin-left: 70px;
  text-align: center;
  width: 100%;
  margin-top: 60px;
  display: inline-block;
  margin-left: 0;
  @include tablet {
    margin-left: 165px;
    margin-right: 89px;
    width: 200px;
  }
  @include desktop {
    text-align: start;
  }
}

.footer-text-soc {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: calc(16 / 14);
  text-transform: uppercase;
  color: var(--color-white);
  text-align: center;
}

.footer-socialmesh-ul {
  margin-top: 20px;
  display: flex;
  gap: 10px;
  justify-content: center;
  @include desktop {
    justify-content: flex-start;
  }

}

.footer-socialmesh-li {
  width: 44px;
  height: 44px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  transition: background var(--trans);

  &:hover {
    background: var(--color-accent);
  }
}

.footer-socialmesh-link {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-socialmesh-icon {
  fill: var(--color-white);
}
